<template>
  <div style="width:100%;height:100%">
    <div class="menu-top">客户管理</div>
    <div class="table-top radius_8">
      <div class="table-top-left">
        <div class="table-top-left-top">
          <div class="div_p">
            <span class="font_size_20">客户名称</span>
            <el-input class="inp" v-model="sel.user_name" placeholder="请输入内容"></el-input>
          </div>
          <div class="div_p">
            <span class="font_size_20">联系方式</span>
            <el-input class="inp" v-model="sel.user_tel" placeholder="请输入内容"></el-input>
          </div>
          <div class="div_p">
            <span class="font_size_20">创建时间</span>
            <el-date-picker class="inp" v-model="sel.create_time" type="date" placeholder="选择日期"></el-date-picker>
          </div>
        </div>
        <div class="table-top-left-btn">
          <div class="div_p">
            <span class="font_size_20">状态</span>
            <el-select class="inp" v-model="sel.user_status" placeholder="请选择">
              <el-option v-for="item in status" :key="item.lab" :label="item.lab" :value="item.val"></el-option>
            </el-select>
          </div>
          <div class="div_p">
            <span class="font_size_20">渠道</span>
            <el-select class="inp" v-model="sel.channel" placeholder="请选择">
              <el-option
                v-for="item in channels"
                :key="item.lab"
                :label="item.lab"
                :value="item.val"
              ></el-option>
            </el-select>
          </div>
          <div class="div_p">
            <span class="font_size_20">销售顾问</span>
            <el-select class="inp" v-model="sel.saller_name" placeholder="请选择">
              <el-option
                v-for="(item,index) in leaders"
                :key="index"
                :label="item==null?'全部':item"
                :value="item"
              ></el-option>
            </el-select>
          </div>
        </div>
      </div>
      <div class="table-top-rig">
        <el-button @click="reset()" class="but">重置</el-button>
        <el-button @click="changeDate()" class="but" type="primary">查询</el-button>
      </div>
    </div>

    <div class="menu-small-top">客户列表</div>
    <div class="table-btn">
      <template>
        <el-table :data="tableData" style="width: 100%" class="font_size_16">
          <!-- type="index" :index="indexMethod" -->
          <el-table-column label="序号" width="100" align="center" prop="NO"></el-table-column>
          <!-- <el-table-column  width="80" align="center" prop="NO"></el-table-column> -->
          <el-table-column prop="user_name" label="客户名称" width="150"></el-table-column>
          <el-table-column prop="user_tel" label="联系方式" width="150"></el-table-column>
          <el-table-column prop="create_time" width="150" label="创建时间"></el-table-column>
          <el-table-column prop="user_status" width="150" label="客户状态"></el-table-column>
          <el-table-column prop="source" width="150" label="客户来源"></el-table-column>
          <el-table-column prop="channel" width="150" label="获取渠道"></el-table-column>
          <el-table-column prop="purpose" width="150" label="购房用途"></el-table-column>
          <el-table-column prop="saller_name" width="150" label="销售顾问"></el-table-column>
          <el-table-column prop="follow_time" width="150" label="上次跟进时间"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button class="font_size_14" v-if="user['auth']!=100" @click="showTab(scope.row)" type="text" size="small">查看详情</el-button>

              <el-button class="font_size_14" v-if="user['auth']==200" @click="set(scope.row)" type="text" size="small">新建跟进</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <div   class="page">
        <el-pagination
          background
          layout="total,prev, pager, next,jumper"
          @current-change="handleCurrentChange"
          :page-size="sel.pre_page"
          :current-page="sel.page_num"
          :total="total"
        ></el-pagination>
      </div>
      <!--       :page-count="count" -->
    </div>
    <el-dialog 
     :modal-append-to-body='false'
    :title="tabName" :visible.sync="isDia" width="420px" :before-close="closeSet">
     <el-form  :model="data" :rules="rules" ref="ruleForm" label-width="130px" class="demo-ruleForm">
          <el-form-item label="新建跟进" prop="memo">
             <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              v-model="data.memo">
            </el-input>
          </el-form-item>
     </el-form>
      <!-- <div class="upPaw">
         <div class="set-role">
          <span>新建跟进</span>
          <div class="set-role-right">
            <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              v-model="data.memo">
            </el-input>

          </div>
        </div>
      </div> -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeSet()">取 消</el-button>
        <el-button type="primary" @click="upOk('ruleForm')" :loading="isLoad">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { setStore, getStore } from "@/config/mUtils";
import {
  getSallers,
  getCustomers,
  getCustomer,
  addMemo,
  getMemos,
  getLeader
} from "@/api/getData";
export default {
  name: "cus",

  data() {
    return {
      user:null,
      // 可修改不可修改
      dis: false,
      isLoad: false,
      // count: 10, //总页数
      total: 100, //总条数
      value: "",
      radio: "",
      time: "",
      isDia: false,
      sel: {
        user_name: null,
        user_tel: null,
        create_time: null,
        user_status: null,
        channel: null,
        saller_name: null,
        pre_page: 10, //每页条数
        page_num: 1 //第几页
      },
      tabName: "创建团队",
      data: {
       user_id:0,
       memo:null, 
      },
      rules:{
        
           memo: [
            { required: true, message: '跟进内容不可为空', trigger: 'blur' },
          
            { min:2, max: 300, message: '长度在 2 到 300 个字符', trigger: 'blur' }
          ]
      },
      channels: [
         { val: null, lab: "全部" },
        { val: "微信", lab: "微信" },
        { val: "实体广告", lab: "实体广告" },
        { val: "朋友推荐", lab: "朋友推荐" },
        { val: "自己发现", lab: "自己发现" },
        { val: "其他售楼处推荐", lab: "其他售楼处推荐" },
        { val: "电话咨询", lab: "电话咨询" },
        { val: "顾问邀请", lab: "顾问邀请" },
       
      ],
      status: [
         { val: null, lab: "全部" },
        { val: "A类", lab: "A类" },
        { val: "B类", lab: "B类" },
        { val: "C类", lab: "C类" },
        { val: "垃圾", lab: "垃圾" },
        { val: "签约", lab: "签约" },
        { val: "认筹", lab: "认筹" },
        { val: "认购", lab: "认购" },
        { val: "回款", lab: "回款" },
         
      ],
      options: [],
      leaders: [],
      tableData: [],
     
    };
  },
  components: {},
  computed: {},
  created() {
    // this.handleListener();
    // this.changeDate();
    this.getUser();
  },
  async mounted() {
    if (this.$route.query.sel) {
      // console.log(this.$route.query.sel);
      this.sel=this.$route.query.sel;
    } 
    await this.changeDate();
    // await this.chanTeam();
    await this.chanLeader();
  },

  activated() {
    // this.handleListener();
  },
  deactivated() {
    // window.removeEventListener('resize', this.renderChart);
    // bus.$off('collapse', this.handleBus);
  },
  methods: {
    getUser(){
       var data = getStore("user");
        var jsondata = data ? JSON.parse(data) : {};
        this.user=jsondata;
    },
    // 初始化
    init() {
      this.isLoad = false;
      this.data =  {
       user_id:0,
       memo:null, 
      };
    },
    async changeDate() {
      var dt = JSON.stringify(this.sel);
      var jsonDt = JSON.parse(dt);

      // 改变时间格式
      if (jsonDt.create_time) {
        var getTime = new Date(jsonDt.create_time);
        jsonDt.create_time = getTime.toLocaleDateString();
      }
      // 查询数据
      const res = await getCustomers(jsonDt);
      //  console.log(res);
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      this.tableData = res.data.list;
      this.total = res.data.total;
    },
    indexMethod(index) {
      return index + 1 * 1;
    },
    handleCurrentChange(val) {
      //页数发生变化
      // alert(`当前页: ${val}`);
      this.sel.page_num = val;
      this.changeDate();
    },
    async set(ent) {
      this.tabName = "新建跟进";
      this.dis = true;
      this.data.user_id=ent.user_id;
      this.isDia = true;
      this.isLoad = false;
    },
    async showTab(ent) {
      // 查看详情
      this.$router.push({
        name: "followUpRecord",
        query: { user_id:ent.user_id}
        ,params: { sel: this.sel }
      });
      // this.$root('teamDetails')
    },
    add() {
      this.isDia = true;
      this.tabName = "新建跟进";
      this.init();
      this.dis = false;
    },
    closeSet() {
      this.isDia = false;
       this.$refs['ruleForm']?this.$refs['ruleForm'].resetFields():'';
    },
    async upOk(formName) {
       this.$refs[formName].validate(async (valid) => {
        if(valid){
          if (!this.data.memo) {
            this.$message({
              type: "error",
              message: "跟进内容不可为空"
            });
            return;
          }
          if (this.data.memo.length>300) {
            this.$message({
              type: "error",
              message: "跟进内容不可大于300"
            });
            return;
          }
          this.isLoad = true;
          let res;
          if (this.tabName == "新建跟进") {
            res = await addMemo(this.data);
          } 

          this.isLoad = false;
          // console.log(jsondata['build_id'] )
          if (res.code != 0) {
            this.$message({
              type: "error",
              message: res.msg
            });
            return;
          }
          this.$message({
            type: "success",
            message: "设置成功"
          });
          this.init();
          this.changeDate();
          this.isDia = false;
        }

       })
    },
    reset() {
      var te = this.sel;
      te.user_name = null;
      te.user_tel = null;
      te.create_time = null;
      te.user_status = null;
      (te.channel = null), (te.saller_name = null), (this.sel = te);
    },
    // 获取团队
    async chanTeam() {
      var data = getStore("user");
      var jsondata = data ? JSON.parse(data) : {};
      const res = await selectGroups({ build_id: jsondata["build_id"] });
      // console.log(jsondata['build_id'] )
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      if (Array.isArray(res.data)){
            this.options = res.data;
       }
     else{
         this.options=[];
      }
      this.options.unshift(null);
      // console.log(res);
    },
    // 获取领导
    async chanLeader() {
      const res = await getSallers({});
      // console.log(jsondata['build_id'] )
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
        if (!Array.isArray(res.data)){
          this.leaders =[];
           
       }
     
      for (let i = 0; i < res.data.length; i++) {
         this.leaders.push(res.data[i][1]);
        
      }
      this.leaders.unshift(null);
    }
  }
};
</script>



<style scoped>
.grid-content {
  background-color: black;
}
.table-top {
  display: flex;
  justify-content: space-between;
}
.table-top-left {
  width: 70%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.table-top-rig {
  width: 17%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.table-top-left-top,
.table-top-left-btn {
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.div_p {
  width: 26%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.div_p span{
  width:80px;
}
.inp {
  width: 200px;
}

.page {
  display: flex;
  height: 38px;
  margin-right: 10px;
  align-items: center;
  flex-direction: row-reverse;

  /* float: right; */
}
/* 修改 */
.upPaw {
  width: 90%;
  margin-left: 4%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.upPaw div span {
  font-size: 20px;
  /* font-weight: 600; */
  width: 35%;
}
.upPaw_pa {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}
.set-role {
  margin-top: 20px;
  width: 100%;
  height: 30%;
  /* background: rosybrown; */
  display: flex;
}
.set-role span {
}
.set-role-right {
  vertical-align: middle;
  width: 200px;
  /* background: royalblue; */
}
</style>
<style >
.el-table__header {
  padding: 5px 0;
}
.el-table__body-wrapper .el-table__row td {
  padding: 5px 0;
}
.el-input__inner {
  height: 37px;
}
.el-input__icon {
  line-height: 37px;
}
.el-radio {
  margin-top: 5px;
}
.el-radio {
  margin-right: 20px;
}

 .div_p .el-date-editor.el-input {
    width: 200px;
}
</style>



